$background: #1F271B;
$foreground: #F3FCF0;
$yellow: #F5BC00;
$green: #23F0C7;
$coral: #EE4266;


h1 {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 40px;
    text-align: center;
    margin: 2rem;
    color: $foreground;
}
.message {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    color: $foreground;
    font-weight: 700;
    text-align: center;
}
.grid-container {
    width: 18rem;
    position: relative;
    top: 1rem;
    left: 50%;
    transform: translateX(-50%);
}
.row {
    display: flex;
    justify-content: center;
    margin: 0.1rem 0 0 0;
}
.letter {
    width: 50px;
    height: 50px;
    border: 2px solid $foreground;
    margin: 0.5rem;
    border-radius: 10%;
    text-align: center;
    font-size: 42px;
    color: $foreground;
}
#correct {
    background-color: $green;
    color: $background;
}
#almost {
    background-color: $yellow;
    color: $background;
}
#wrong {
    background-color: $coral;
    color: $background;
}
.keyboard {
    position: relative;
    top: 2rem;
    left: 0;
}
.key-row {
    display: flex;
    justify-content: center;
}
.key {
    width: 30px;
    height: 30px;
    border: 1px solid $foreground;
    text-align: center;
    margin: 0.2rem;
    font-weight: 700;
    font-size: 24px;
    background-color: $foreground;
    color: $background;
    border-radius: 10%;
    &:hover {
        cursor: pointer;
        transform: scale(110%);
    }
}
.retry {
    background-color: $foreground;
    position: relative;
    top: 2rem;
    left: 50%;
    transform: translateX(-50%);
    width: 10rem;
    padding: 0.5rem;
    text-align: center;
    border-radius: 10px;
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 2px;
    color: $background;
    font-weight: 700;
    font-size: 20px;
    &:hover {
        cursor: pointer;
    }
}
#big {
    width: 100px;
    border-radius: 5px;
}
#disabled {
    background-color: $coral;
}
#ding {
    background-color: $green;
}
@media (max-width: 488px) {
    h1 {
        margin: 1rem;
    }
    .key {
        width: 25px;
        height: 25px;
        font-size: 20px;
        margin: 0.1rem;
    }
    #big {
        width: 70px;
    }
}